import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ServicesService } from 'src/app/services/services.service';

@Component({
  selector: 'app-surah-detail',
  templateUrl: './surah-detail.component.html',
  styleUrls: ['./surah-detail.component.scss']
})
export class SurahDetailComponent implements OnInit {

  detailSurah = {}

  constructor(private route: ActivatedRoute, private services: ServicesService) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.services.getListSurah(params.id).subscribe(result => {
        this.detailSurah = result
      })
    });
  }

}
