import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './page/home/home.component';
import { SurahComponent } from './page/surah/surah.component';
import { SurahDetailComponent } from './page/surah-detail/surah-detail.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'surah', component: SurahComponent },
  { path: 'surah/:id', component: SurahDetailComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
